.home-card {
    background-color: #FFF;
    min-width: 50px;
    min-height: 50px;
    border-radius: 1rem;
    transition: background-color 0.3s, box-shadow 0.3s;
    box-shadow: 3px 3px 20px -10px rgba(13, 17, 33, .25), 0 1px 8px 0 rgba(27, 36, 72, .16);
}

.home-card img {
    filter: grayscale(100%);
    transition: filter 0.3s;
    object-fit: contain;
}

.home-card:hover {
    background-color: #FFF;
    box-shadow: 3px 3px 20px -10px rgba(13, 17, 33, .25), 0 1px 2px 0 rgba(27, 36, 72, .16);
}

.home-card:hover img {
    filter: grayscale(0%);
}

.home-background-animation {
    overflow: hidden;
    max-height: 100%;
    display: flex;
    align-items: flex-end;
    position: relative;
}

.home-background-container {
    position: relative;
}

.home-background-text {
    position: absolute;
    top: 5%;
}
